<template>
  <div>
    <CoverPage />
    <Scroller />
    <About />
    <PassionProject />
    <UniWork />
    <SpotifyWidget />
  </div>
</template>

<script>
import CoverPage from "../components/CoverPage";
import Scroller from "../components/Scroller.vue";
import About from "../components/About";
import UniWork from "../components/UniWork";
import SpotifyWidget from "../components/SpotifyWidget";
import PassionProject from "../components/PassionProjects";

import { TimelineMax } from "gsap";

export default {
  name: "Home",
  components: {
    CoverPage,
    Scroller,
    About,
    PassionProject,
    UniWork,
    SpotifyWidget,
  },
  mounted() {
    console.log(
      "%c🌴 Welcome to the club! 🌴",
      "padding: 0.3em; background-color; color: white; font-family: sans-serif; font-size: 1.3em; border-radius: 0.2em; background: gray; background: linear-gradient(90deg, rgba(131,58,180,1) 0%, rgba(253,29,29,1) 50%, rgba(252,176,69,1) 100%);"
    );
    const tl = new TimelineMax();

    const faders = document.querySelectorAll(".fade-in");
    const sliders = document.querySelectorAll(".slide-in");

    const appearOptions = {};

    const appearOnScroll = new window.IntersectionObserver(
      (entries, appearOnScroll) => {
        entries.forEach((e) => {
          if (!e.isIntersecting) return;
          tl.to(e.target, {
            y: -50,
            opacity: 1,
            duration: 0.4,
          });
          appearOnScroll.unobserve(e.target);
        });
      },
      appearOptions
    );

    const slideInOnScroll = new window.IntersectionObserver(
      (entries, appearOnScroll) => {
        entries.forEach((e) => {
          if (!e.isIntersecting) return;
          tl.to(e.target, {
            x: 0,
            opacity: 1,
            duration: 0.6,
          });
          appearOnScroll.unobserve(e.target);
        });
      },
      appearOptions
    );

    faders.forEach((e) => appearOnScroll.observe(e));
    sliders.forEach((e) => slideInOnScroll.observe(e));
  },
};
</script>

<style>
.fade-in {
  opacity: 0;
}

.slide-in {
  opacity: 0;
}
</style>
