<template>
  <div>
    <div class="title fade-in">Passion Projects</div>
    <div class="passion-project-container">
      <div class="empty-container"></div>
      <div :key="proj.description" v-for="proj in projects">
        <PassionProjectComponent :project="proj" />
      </div>
    </div>
    <div class="text-container fade-in">
      <p>
        Over the summer I started building and designing more websites for fun,
        learning new technologies such as ReactJS and building on my
        pre-existing knowledge of Vue & Flutter. I also started shifting some
        focus to graphic design and started building a folio of work.
      </p>

      <p>Click an image for more info.</p>
    </div>
  </div>
</template>
<script>
import PassionProjectComponent from "./PassionProjectComponent";
import $ from "jquery";

export default {
  name: "PassionProjects",
  components: {
    PassionProjectComponent,
  },
  mounted() {
    const scrollContainer = document.querySelector(
      ".passion-project-container"
    );

    const onScroll = () => {
      var totalOffset = this.$el.getBoundingClientRect().top;
      var windowOffset = $(window).scrollTop() - totalOffset;
      var relativeOffset = windowOffset / $(window).innerHeight();
      scrollContainer.scrollLeft = (relativeOffset - 1) * 100;
    };
    window.addEventListener("scroll", onScroll);
    scrollContainer.addEventListener("click", () => {
      window.removeEventListener("scroll", onScroll);
    });
  },
  data: function() {
    return {
      projects: [
        {
          name: "Tyler Webpage",
          description:
            "The primary objective of this site was to help teach myself the ReactJS framework. I included GSAP animations which I had previously used when building my folio website, but this helped integrate these skills with the functionality of React",
          directory: "assets/passion-project-images/tyler-webpage",
          size: "large",
          links: [
            {
              value: "See More",
              clickAction: () => {
                window.open(
                  "https://dribbble.com/shots/14374790-Tyler-the-Creator-Web-Concept?utm_source=Clipboard_Shot&utm_campaign=moyes&utm_content=Tyler%20the%20Creator%20Web%20Concept&utm_medium=Social_Share",
                  "_blank"
                );
              },
            },
            {
              value: "assets/github-logo.png",
              isImage: true,
              clickAction: () => {
                window.open(
                  "https://github.com/jamiemoyes/tyler-webpage",
                  "_blank"
                );
              },
            },
          ],
          hoverAction: () => {},
        },
        {
          name: "Instagram",
          description:
            "During lockdown I set up a platform for my work on Instagram, somewhere to publish anything I create. This has motivated me to create more and be more proactive with my time",
          directory: "assets/passion-project-images/instagram",

          links: [
            {
              value: "See More",
              clickAction: () => {
                window.open("https://www.instagram.com/moyes.io/", "_blank");
              },
            },
          ],
        },
        {
          name: "Punked Concept",
          description:
            "This is a concept I designed to use along with the PunkAPI - an open source API for beverages made by Brewdog.",
          directory: "assets/passion-project-images/brewdog-webpage",
          size: "large",

          links: [
            {
              value: "See More",
              clickAction: () => {
                window.open(
                  "https://dribbble.com/shots/14377622-Punked-Concept?utm_source=Clipboard_Shot&utm_campaign=moyes&utm_content=Punked%20Concept&utm_medium=Social_Share",
                  "_blank"
                );
              },
            },
          ],
        },
      ],
    };
  },
};
</script>

<style scoped>
.passion-project-container {
  width: 100vw;
  overflow-x: scroll;

  display: flex;
  /* flex-wrap: wrap; */
}

.empty-container {
  width: 20em;
}

.title {
  margin-left: 4em;
  font-family: "Asul", sans-serif;
  font-weight: 700;
  font-size: 3em;
  text-align: left;
}

.text-container {
  margin: 4em 22em;
  width: 40em;
  font-family: "Montserrat";
}

.text-container p {
  text-align: justify;

  margin-bottom: 1em;
}

@media only screen and (max-width: 1000px) {
  .text-container {
    margin-left: 0;
    text-align: justify;
    width: 70%;
    padding: 0 15vw;
  }

  .title {
    font-size: 2.5em;
    margin-left: 15vw;
    padding-bottom: 0;
  }
}
</style>
