<template>
  <div class="headings-container">
    <span class="title">JMOYES</span>

    <div class="header-row">
      <nav>
        <a
          :key="label.id"
          :href="label.link"
          :target="label.target"
          class="nav-link"
          v-for="label in this.headings"
        >
          <span>{{ label.name }}</span>
        </a>
        <div class="cursor"></div>
        <div class="outer-cursor"></div>
      </nav>
    </div>
  </div>
</template>

<script>
import { TimelineMax, TweenMax } from "gsap";

export default {
  name: "PageHeading",
  props: ["headings"],
  mounted() {
    const link = document.querySelectorAll("nav > .nav-link");
    const cursor = document.querySelector(".cursor");
    const outerCursor = document.querySelector(".outer-cursor");

    let clientX = -100;
    let clientY = -100;

    const animateIt = function(e) {
      const span = this.querySelector("span");
      const { offsetX: x, offsetY: y } = e,
        { offsetWidth: width, offsetHeight: height } = this,
        move = 2,
        xMove = (x / width) * (move * 2) - move,
        yMove = (y / height) * (move * 2) - move;

      span.style.transform = `translate(${xMove}px, ${yMove}px)`;
      if (e.type === "mouseleave") span.style.transform = "";
    };

    const initCursor = () => {
      window.addEventListener("mousemove", (e) => {
        clientX = e.clientX;
        clientY = e.clientY;
      });

      const editCursor = () => {
        TweenMax.to(cursor, {
          left: clientX,
          top: clientY,
        });

        outerCursor.style.top = clientY + "px";
        outerCursor.style.left = clientX + "px";

        requestAnimationFrame(editCursor);
      };
      requestAnimationFrame(editCursor);
    };

    initCursor();

    link.forEach((b) => b.addEventListener("mousemove", animateIt));
    link.forEach((b) => b.addEventListener("mouseleave", animateIt));

    const tl = new TimelineMax({ paused: true });

    tl.to(
      ".nav-link, .title",
      {
        opacity: 1,
        duration: 2,
        delay: 5,
        stagger: 0.2,
      },
      "start"
    );

    tl.restart();
  },
};
</script>

<style scoped>
span {
  font-family: "Asul", sans-serif;
  font-size: 1.5em;
  font-weight: 900;
  display: inline;
  margin: 0 5em 0 0;
}

.header-row {
  cursor: none;
  display: flex;
  justify-content: flex-end;
}

.nav-link {
  opacity: 0;
  transition: all 0.3s ease;
}

span {
  display: inline-block;

  pointer-events: none;
  transition: transform 0.1s linear;
  /* text-decoration: none; */
  color: white;
}

.cursor {
  pointer-events: none;
  position: fixed;
  padding: 0.3rem;
  background-color: #fff;
  border-radius: 50%;
  mix-blend-mode: difference;
  transition: transform 0.3s ease;
  z-index: 99;
  transform: translate(50%, 50%);
}

.outer-cursor {
  pointer-events: none;
  position: fixed;
  padding: 0.8rem;
  opacity: 0.4;
  border: solid 2px white;
  border-radius: 50%;
  z-index: 99;
}

.nav-link:hover ~ .cursor {
  transform: translate(-50%, -50%) scale(3);
}

.title {
  font-family: "Asul", sans-serif;
  -webkit-text-stroke: white 1px;
  -webkit-text-fill-color: transparent;
  font-size: 2em;
  margin-left: 2em;
  opacity: 0;
  /* float: left; */
}
.headings-container {
  display: flex;
  justify-content: space-between;
  margin-top: 2em;
}
</style>
