<template>
  <div class="container">
    <div></div>
    <div class="title">
      <!-- <router-link to="/" class="link">⇖</router-link> -->
      Tyler Webpage
    </div>

    <div class="video-container one">
      <video
        muted="true"
        src="../assets/passion-project-images/tyler-webpage/home-page-demo.webm"
        type="video/webm"
        class="fade-in"
      />
      <div class="video-description fade-in">
        After creating mock designs for this idea I began to look at React
        tutorials and decided to proceed with it. GSAP was used to create smooth
        animations and transition between parts of the screen. It took me a
        while to understand the use of hooks but after practising them in other
        tutorials I got the hang of them. They were used to create reusable
        animations that were applied to different parts of the page.
      </div>
    </div>
    <div class="video-container two">
      <video
        muted="true"
        src="../assets/passion-project-images/tyler-webpage/scrolling-demo.webm"
        type="video/webm"
        class="fade-in"
      />
    </div>
    <div class="video-container three">
      <video
        muted="true"
        src="../assets/passion-project-images/tyler-webpage/cursor-demo.webm"
        type="video/webm"
        class="fade-in"
      />

      <div class="video-description fade-in">
        I also created a custom mouse for this page with different interactions
        depening on the area of the screen. This was created mainly using CSS
        with Javascript used for the different movements & interactions.
      </div>
    </div>
    <div class="link-container fade-in">
      <div class="see-more" @click="openLink('https://www.google.com')">
        FULL CODE HERE
      </div>
    </div>
  </div>
</template>

<script>
import { TimelineMax } from "gsap";

export default {
  name: "TylerProject",
  mounted: () => {
    const tl = new TimelineMax();

    const faders = document.querySelectorAll(".fade-in");

    const appearOptions = {};

    const appearOnScroll = new window.IntersectionObserver(
      (entries, appearOnScroll) => {
        entries.forEach((e) => {
          if (!e.isIntersecting) return;
          tl.to(e.target, {
            y: -50,
            opacity: 1,
            duration: 0.4,
          });

          if (e.target.tagName.toLowerCase() === "video") {
            e.target.play();
          }

          appearOnScroll.unobserve(e.target);
        });
      },
      appearOptions
    );

    faders.forEach((e) => appearOnScroll.observe(e));
  },
  methods: {
    openLink(link) {
      window.open(link, "_blank");
    },
  },
  //   components: { CursorVideo, HomePageVideo, ScrollingDemo },
};
</script>

