<template>
  <div class="cover-page">
    <ResponsiveHeading v-if="this.mobile" v-bind:headings="this.headings" />
    <PageHeading v-else v-bind:headings="this.headings" />
    <CenterAnimation />
  </div>
</template>

<script>
import PageHeading from "./PageHeading";
import CenterAnimation from "./CenterAnimation";
import ResponsiveHeading from "./ResponsiveHeading";
export default {
  name: "CoverPage",
  components: {
    PageHeading,
    CenterAnimation,
    ResponsiveHeading,
  },
  data() {
    return {
      headings: [
        // { id: 0, name: "Work", link: "#work-section", target: "" },
        {
          id: 1,
          name: "Resume",
          link: "assets/Jamie Moyes CV 1.pdf",
          target: "_blank",
        },
      ],
      mobile: window.innerWidth <= 600,
    };
  },
  created() {
    addEventListener("resize", () => {
      this.mobile = innerWidth <= 600;
    });
  },
};
</script>

<style scoped>
.cover-page {
  height: 100vh;
}

@media only screen and (max-width: 600px) {
  .cover-page {
    height: calc(100vh + 10em);
  }
}
</style>
