<template>
  <div class="center-wrapper">
    <div class="circle-image">
      <video
        muted="true"
        src="assets/Masked.mp4"
        type="video/mp4"
        playsinline
      />
    </div>
    <div class="row-container">
      <span class="row">Nice</span>
      <span class="row">To</span>
      <span class="row">See</span>
      <span class="row">You</span>
    </div>
  </div>
</template>

<script>
import { gsap, TimelineMax } from "gsap";
import $ from "jquery";
export default {
  name: "CenterAnimation",
  mounted() {
    const tl = new TimelineMax({ paused: true });
    var offset = 50;

    /// Increase opacity
    gsap.from(".center-wrapper", {
      duration: 3,
      opacity: 0,
      delay: 1,
    }),
      gsap.from(".row", {
        duration: 3,
        opacity: 0,
        delay: 4,
      });

    /// Move text apart
    document.querySelectorAll(".row").forEach((row, i) => {
      if (i % 2 == 0) {
        //from left

        tl.to(
          row,
          {
            x: -offset * (4 - i),
            duration: 2,
          },
          "5"
        );
      } else {
        tl.to(
          row,
          {
            x: offset * (4 - i),
            duration: 2,
          },
          "5"
        );
      }
    });

    tl.restart();

    $(window).scroll(() => {
      tl.pause();
      var prog = 1 - $(window).scrollTop() / 1000;
      tl.progress(prog);
    });

    /// TODO this isn't centered with animated text :(
    var video = this.$el.querySelector("video");
    video.play();

    $(window).scroll(() => {
      var y = $(window).scrollTop();
      var perc = Math.min(y / window.innerHeight, 1);
      video.currentTime = video.duration * (1 - perc);
    });
  },
};
</script>

<style scoped>
span {
  font-family: "Chivo", sans-serif;
  font-weight: 900;
  font-size: max(5em, 8vh);
  text-transform: uppercase;
  display: block;
  text-align: center;
  margin: 0 auto;
  z-index: 10;
  -webkit-text-stroke: 2px white;
  -webkit-text-fill-color: transparent;
}

.row-container {
  margin: 0;
  padding: max(25vh, 10em) 0;
  overflow-x: hidden;
}

.circle-image {
  top: max(25vh, 10em);
  position: absolute;
  z-index: -1;
  left: 50%;
  transform: translate(-50%);
  overflow: hidden;
  text-align: center;
  vertical-align: center;
}

video {
  clip-path: circle();
  width: 100%;
  max-width: max(50vh, 30em);
}

@media only screen and (max-width: 600px) {
  .circle-image {
    left: 0;
    transform: none;
  }
  .row-container {
    margin-top: 15vh;
    padding: 0;
  }
}
</style>
