<template>
  <div>
    <div class="heading-container">
      <i v-on:click="openMenu" class="material-icons open">menu</i>
    </div>

    <div class="menu">
      <div class="icon-container">
        <i class="material-icons close" v-on:click="closeMenu">close</i>
      </div>
      <div class="links">
        <a
          v-on:click="closeMenu"
          :key="label.id"
          :href="label.link"
          class="nav-link"
          v-for="label in this.headings"
        >
          <span>{{ label.name }}</span>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import gsap from "gsap";

export default {
  name: "ResponsiveHeading",
  props: ["headings"],
  mounted() {
    gsap.to(".heading-container", {
      opacity: 1,
      duration: 2,
      delay: 5,
      stagger: 0.2,
    });
  },
  methods: {
    openMenu() {
      const links = document.querySelectorAll(".menu a");
      const icon = document.querySelector(".close");

      gsap.to(".menu", {
        opacity: 1,
        display: "block",
        duration: 0.5,
      });

      gsap.to(links, {
        opacity: 1,
        stagger: 0.3,
        delay: 1,
        y: -50,
      });

      gsap.to(icon, {
        opacity: 1,
        delay: 1,
        duration: 0.5,
      });
    },
    closeMenu() {
      const links = document.querySelectorAll(".menu a");
      gsap.to(".menu", {
        opacity: 0,
        display: "none",
        duration: 0.5,
        delay: 1,
      });

      gsap.to(links, {
        opacity: 0,
        stagger: 0.3,
        x: -50,
      });

      gsap.to(".close", {
        opacity: 0,
        duration: 0.5,
      });

      gsap.to(links, {
        x: 0,
        y: 0,
        delay: 1,
      });
    },
  },
};
</script>

<style scoped>
.heading-container {
  text-align: right;
  padding: 1em;
  opacity: 0;
}
i {
  font-size: 3em;
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10+ and Edge */
  user-select: none;
}

.menu {
  position: fixed;
  width: 100%;
  height: 100%;
  background: #101010;
  z-index: 99;
  top: 0;
  transition: 0.3s;
  opacity: 0;
  display: none;
}

.menu a {
  width: 50%;
  opacity: 0;

  font-family: "Asul", sans-serif;
  font-size: 2em;
  color: white;
  display: block;
  text-align: left;
  margin-bottom: 1em;
  text-decoration: none;
}

.links {
  /* margin-top: 3em; */
  padding-left: 3em;
}

.menu i {
  float: right;
  text-align: right;
}

.icon-container {
  display: block;
  text-align: right;
  overflow: hidden;
  padding: 1em;
}

.close {
  opacity: 0;
}
</style>
