<template>
  <div class="spotify-container">
    <div class="content">
      <div class="title slide-in">What I'm Listening To</div>
      <span class="desc slide-in"
        >Some sounds I enjoy listening while working</span
      >
    </div>

    <div class="carousel fade-in">
      <div class="empty-div"></div>
      <div
        v-bind:key="playlist.id"
        v-for="playlist in this.playlists"
        class="playlist"
      >
        <a :href="playlist.link" target="_blank">
          <div class="image-container">
            <img :src="playlist.image" />
          </div>
          <span class="playlist-name">{{ playlist.title }}</span>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  name: "SpotifyWidget",
  mounted() {
    const scrollContainer = document.querySelector(".carousel");
    $(window).scroll(() => {
      var totalOffset = this.$el.getBoundingClientRect().top;
      var windowOffset = $(window).scrollTop() - totalOffset;
      var relativeOffset = windowOffset / $(window).innerHeight();
      scrollContainer.scrollLeft =
        (relativeOffset - 1) * 100 - $(window).innerWidth() * 0.9;
    });
  },
  data() {
    return {
      playlists: [
        {
          id: 0,
          title: "Classix",
          image: "assets/drift.jpg",
          link: "https://open.spotify.com/playlist/2QSkORvVu8GFWE8uPqkKS8",
        },
        {
          id: 1,
          title: "Trad",
          image: "assets/mountains.jpg",
          link: "https://open.spotify.com/playlist/4ygtIeSGOIsgGHrJWdYmXL",
        },
        {
          id: 2,
          title: "Alt. Chill",
          image: "assets/moon.jpg",
          link: "https://open.spotify.com/playlist/2QSkORvVu8GFWE8uPqkKS8",
        },
      ],
    };
  },
};
</script>

<style scoped>
.spotify-container {
  height: 30em;
  text-align: left;
  display: flex;
  justify-content: space-around;
  /* overflow: hidden; */
}

.empty-div {
  width: 20em;
}

.title {
  font-family: "Asul";
  font-weight: 700;
  font-size: 2em;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

.content {
  padding: 10vh 15vw;
  font-family: "Montserrat";
}

.carousel {
  /* background: white; */
  width: 50vw;
  display: flex;
  overflow-x: scroll;
}

.image-container {
  height: 20em;
  width: 20em;
  overflow: hidden;
  -webkit-text-stroke: 1px white;
}

.image-container img {
  height: 100%;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

.playlist {
  margin: 2em;
}

.playlist a {
  text-decoration: none;
}

.playlist-name {
  font-family: "Asul";
  font-size: 3em;
  color: white;
  text-decoration: none;
  display: inline-block;
  transform: translate(10%, -50%);
}

.desc {
  display: inline-block;
  margin-top: 2em;
}

@media only screen and (max-width: 1000px) {
  .carousel {
    width: 100vw;
  }

  .spotify-container {
    flex-wrap: wrap;
  }
}
</style>
