<template>
  <div class="uni-work" name="work-section" id="work-section">
    <span class="title fade-in"> Uni Projects </span>

    <Project
      v-bind:key="project.id"
      v-bind:project="project"
      v-for="project in this.projects"
    />
  </div>
</template>

<script>
import Project from "./Project";
import $ from "jquery";
export default {
  name: "UniWork",
  components: {
    Project,
  },
  data() {
    return {
      projects: [
        {
          id: 0,
          name: "Simplified Bus Routes",
          description: [
            "A Flutter app for users with Down’s Syndrome to aid the use of public bus transportation.",
            "Built to simplify tasks such as reading bus timetables, self-orientation using visual guides like images of surrouding points of interest, and constant feedback on the user's progress.",
            "Submitted for my 4th year project",
          ],
          img: "assets/project-images/homepage.png",
          link:
            "https://gitlab.cis.strath.ac.uk/rqb16188/simplified_bus_routes_1",
        },
        {
          id: 1,
          name: "Currency Converter",
          description: [
            "Browser-based Graphical currency converter extracting data from the ECB api to get live rates.",
            "Made for app development class.",
          ],
          img: "assets/project-images/currency-converter.png",
          link:
            "https://devweb2018.cis.strath.ac.uk/~rqb16188/currencycalc/Calculator/",
        },

        {
          id: 2,
          name: "Breakout Game",
          description: [
            "Browser-based Atari style breakout game for mobiles. ",
            "Made for 3rd year app development class.",
          ],
          img: "assets/project-images/breakout.png",
          link: "https://devweb2018.cis.strath.ac.uk/~rqb16188/breakout/",
        },
      ],
    };
  },
  mounted() {
    var line = document.querySelector(".follow-me");
    var length = line.getTotalLength();

    var startHeight = $(".uni-work").offset().top;

    line.style.strokeDasharray = length;
    line.style.strokeDashoffset = length;
    $(window).scroll(() => {
      var scrollPerc =
        ($(window).scrollTop() + startHeight) / $(window).height();

      var draw = length * scrollPerc;
      line.style.strokeDashoffset = draw;
    });
  },
};
</script>

<style scoped>
.uni-work {
  text-align: left;
}

svg {
  position: absolute;
  left: 0;
  fill: none;
  stroke: white;
  z-index: -1;
  height: 200vh;
}
.title {
  margin-left: 4em;
  font-family: "Asul", sans-serif;
  font-weight: 700;
  font-size: 3em;
  width: 6em;
  padding-bottom: 1em;
}

.fade-in {
  opacity: 0;
}

@media only screen and (max-width: 600px) {
  .title {
    font-size: 2em;
    margin-left: 1em;
  }
}
</style>
