<template>
  <div v-on:mouseleave="onMouseLeave">
    <div class="passion-project  fade-in">
      <img
        v-on:click="displayInfoBox"
        :src="image"
        v-bind:class="{ square: !project.size }"
      />
    </div>

    <div class="info-box">
      <div class="arrow" @click="onMouseLeave">⇖</div>
      <div class="title">
        {{ project.name }}
      </div>
      <div class="info">
        {{ project.description }}
      </div>

      <div
        v-for="link in project.links"
        :key="link"
        class="see-more"
        @click="link.clickAction"
      >
        <span v-if="link.isImage">
          <img :src="require(`@/${link.value}`)" />
        </span>
        <span v-else>
          {{ link.value }}
        </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "PassionProjectComponent",
  props: ["project"],
  data() {
    return {
      image: this.project.directory + "/thumbnail.png",
    };
  },
  methods: {
    displayInfoBox: function(e) {
      console.log("Displaying info-box on " + this.project.name + " image");
      const x = e.pageX;
      const y = e.pageY;

      const box = this.$el.querySelector(".info-box");
      box.style.display = "block";
      box.style.transition = "0.4s";
      box.style.left = `${x}px`;
      box.style.top = `${y}px`;
    },
    onMouseLeave: function() {
      const box = this.$el.querySelector(".info-box");
      box.style.display = "none";
    },
  },
};
</script>

<style scoped>
.passion-project img {
  height: 30em;
  object-fit: cover;
}

.title {
  font-family: "Asul", sans-serif;
  font-weight: 700;
  font-size: 1.4em;
  padding-bottom: 1em;
  text-align: left;
}

.square {
  width: 30em;
}

.passion-project {
  padding: 2em;
  padding-bottom: 0;
}

.info-box {
  font-family: "Montserrat";
  color: black;
  padding: 1em;
  transition: opacity 0.4s;
  background: #ffd9b7;
  width: 13em;
  position: absolute;
  top: 0;
  left: 0;
  display: none;
  z-index: 90;
}

.info-box .info {
  text-align: justify;
  font-size: 0.8em;
}

.arrow {
  font-size: 1.5em;
  transform: translateY(-25%);
  text-align: left;
}

.info-box .see-more {
  text-align: right;
  margin-top: 1em;
  padding: 0.5em;
  float: right;
  transition: background-color 0.4s;
  color: black;
  text-decoration: none;
}

.info-box img {
  width: 1em;
}

.info-box .see-more:hover {
  background-color: #ff9a40;
}
</style>
